.excel-theme {
  &.editable-table {
   
    .editable-cell {
      display: block;
      width: 100%;
      height: 100%;
      cursor: text;

      -moz-user-select: -moz-none;
      -khtml-user-select: none;
      -webkit-user-select: none;

      &.selected {
        background-color: skyblue;
      }

      input {
        display: block;
        width: calc(100% - 4px);
        height: calc(100% - 2px);
        border: 0;
        margin: 0;
        padding: 0;
        border: solid black 1px;
        outline: none;
        text-align: right;
        padding-right: 2px;
      }
    }
  }
}
